import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { IPrePaidPart } from '../../../../blocks/InterFAX/Models/admin';

@Component({
  selector: 'app-select-prepaid-card',
  templateUrl: './select-prepaid-card.component.html',
  styleUrls: ['./select-prepaid-card.component.scss'],
})
export class SelectPrepaidCardComponent implements OnInit {
  @Input() prePaidParts: Array<IPrePaidPart>;
  @Input() enabled: boolean;
  @Input() selected: IPrePaidPart;

  @Output() onPartSelected = new EventEmitter<IPrePaidPart>();
  @Output() valid = new EventEmitter<boolean>();

  constructor() {}

  ngOnInit(): void {
    this.update();
  }

  onSelectPart = (part: IPrePaidPart): void => {
    this.selected = part;
    this.onPartSelected.next(part);

    this.update();
  };

  update = (): void => {
    this.valid.next(!!this.selected);
  };

  includePartInListing = (part: IPrePaidPart): boolean => {
    return true;

    //part.basePart.availableForNewUser
  };
}
