import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-section-terms',
  templateUrl: './section-terms.component.html',
  styleUrls: ['./section-terms.component.scss'],
})
export class SectionTermsComponent implements OnInit {
  @Output() agreed = new EventEmitter<boolean>();
  @Input() brandedServiceTermsUrl: string;
  @Input() showIcon: boolean = true;
  agreedToTerms: boolean;

  constructor() { }

  ngOnInit(): void { }

  onAgreedToTermsChanged = (): void => {
    this.agreed.next(this.agreedToTerms);
  };
}
