import { Component, OnInit, Input } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { InboundService } from '../../../../blocks/InterFAX/Models/admin';

@Component({
  selector: 'app-service-charge',
  templateUrl: './service-charge.component.html',
  styleUrls: ['./service-charge.component.scss'],
})
export class ServiceChargeComponent implements OnInit {
  @Input() inbound: InboundService;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {}

  getChargeText = (cost: string): string => {
    if (!cost) return this.translateService.instant('fax-user-create.none');

    return cost;
  };
}
