import { ChangeDetectorRef, Component, EventEmitter, Input, KeyValueDiffers, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Email } from '../../../../blocks/core/email';
import { CountryZones, InboundService } from '../../../../blocks/InterFAX/Models/admin';

@Component({
  selector: 'app-service-inbound-options',
  templateUrl: './service-inbound-options.component.html',
  styleUrls: ['./service-inbound-options.component.scss'],
})
export class ServiceInboundOptionsComponent implements OnInit {
  @Input() inbound: InboundService;

  private _countryZones: Array<CountryZones>;
  @Input()
  set countryZones(countryZones: Array<CountryZones>) {
    this._countryZones = countryZones;
    if (!this.countryDataSource) return;
    this.updateCountryDataSource();
  }
  get countryZones(): Array<CountryZones> {
    return this._countryZones;
  }

  @Output() valid = new EventEmitter<boolean>();

  fieldInboundEmailTo: FieldDefinition;
  fieldInboundCountryForFaxNumber: FieldDefinition;
  fieldInboundCountryLocation: FieldDefinition;

  zoneDataSource: FixedDataSource;
  countryDataSource: FixedDataSource;

  constructor(private translateService: TranslateService, private cdr: ChangeDetectorRef) {}

  ngOnInit(): void {
    const zone = this.inbound.zone;
    this.zoneDataSource = new FixedDataSource();
    this.loadZoneDataSource(this.inbound.IPVNodeId);
    this.createFields();
    this.updateValidity();
    if (this.inbound.countryCode) {
      this.onCountryCodeChanged(this.inbound.countryCode);
      if (zone) {
        this.onCountryLocationChanged(zone);
      }
    }
  }

  updateValidity = (): void => {
    let valid = true;

    if (this.inbound.enabled) {
      if (!this.inbound.IPVNodeId) valid = false;
      if (!this.inbound.prefix) valid = false;
      if (this.inbound.email && !Email.isValid(this.inbound.email)) valid = false;
    }

    this.valid.next(valid);
    this.cdr.detectChanges();
  };

  onInboundEmailChanged = (): void => {
    this.updateValidity();
  };

  onCountryLocationChanged = (zoneNode: string) => {
    this.inbound.zone = zoneNode;
    const countryZone = this.countryZones.find((cz) => cz.countryCode == this.inbound.countryCode);
    const zone = countryZone.zones.find((z) => z.value == zoneNode);

    this.inbound.setupCharge = zone.node.setPrice;
    this.inbound.subscriptionCharge = zone.node.subPrice;
    this.inbound.faxnumberDetails = `${countryZone.country} - ${zone.name}`;

    this.updateInboundService();
  };

  onCountryCodeChanged = (countryCode: string) => {
    this.inbound.countryCode = countryCode;
    this.loadZoneDataSource(countryCode);

    this.updateInboundService();
  };

  updateInboundService = (): void => {
    const cz = this.countryZones.find((cz) => cz.countryCode == this.inbound.countryCode);
    const z = cz ? cz.zones.find((z) => z.value == this.inbound.zone) : null;

    this.inbound.IPVNodeId = z ? z.node.id : '';
    this.inbound.prefix = z ? z.prefix : '';

    this.updateValidity();
  };

  createFields = (): void => {
    this.fieldInboundEmailTo = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-create.inbound-email-to'),
      placeholder: '',
      required: false,
    });

    this.countryDataSource = this.getCountriesDataSource();

    this.fieldInboundCountryForFaxNumber = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-user-create.inbound-country-for-faxnumber'),
      placeholder: '',
      required: true,
      datasource: this.countryDataSource,
    });

    this.fieldInboundCountryLocation = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-user-create.inbound-country-for-faxnumber-location'),
      placeholder: '',
      required: true,
      datasource: this.zoneDataSource,
    });
  };

  getCountriesDataSource = (): FixedDataSource => {
    const options = new Array<IFieldOptions>();

    for (const cz of this.countryZones) {
      options.push({
        name: this.translateService.instant(`Countries.${cz.country.replace(/[^a-zA-Z0-9]/g, '')}`),
        value: cz.countryCode,
      });
    }

    return new FixedDataSource(options);
  };

  updateCountryDataSource = (): void => {
    this.countryDataSource.updateOptions(this.getCountriesDataSource().options);
  };

  loadZoneDataSource = (countryCode: string): void => {
    const options = new Array<IFieldOptions>();

    const countryZone = this.countryZones.find((cz) => cz.countryCode == countryCode);
    if (countryZone) {
      for (const zone of countryZone.zones) {
        options.push(zone);
      }
    }

    this.zoneDataSource.updateOptions(options);
  };
}
