import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Email } from '../../../../blocks/core/email';
import { OutboundService } from '../../../../blocks/InterFAX/Models/admin';

@Component({
  selector: 'app-service-outbound-options',
  templateUrl: './service-outbound-options.component.html',
  styleUrls: ['./service-outbound-options.component.scss'],
})
export class ServiceOutboundOptionsComponent implements OnInit {
  @Input() outbound: OutboundService;

  @Output() valid = new EventEmitter<boolean>();

  fieldAllowedSender: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
    this.updateValidity();
  }

  updateValidity = (): void => {
    let valid = true;

    if (this.outbound.enabled) {
      if (this.outbound.allowedSender && !Email.isValid(this.outbound.allowedSender)) valid = false;
    }

    this.valid.next(valid);
  };

  onAllowedSenderChanged = (): void => {
    this.updateValidity();
  };

  createFields = (): void => {
    this.fieldAllowedSender = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-create.outbound-allowed-senders'),
      placeholder: '',
      required: false,
    });
  };
}
