import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IPurchaseSummary } from '../../../../blocks/InterFAX/Models/admin';
import { RolePermissions } from '../../../../../../../../Bamba.Shared/src/models/role';
import { UserStateService } from '../../../../blocks/services/user-state.service';

export interface ISummaryProvider {
  load(successCallBack: (id: IPurchaseSummary) => void, failureCallBack: (err: any) => void): void;
}

@Component({
  selector: 'app-parts-invoice',
  templateUrl: './parts-invoice.component.html',
  styleUrls: ['./parts-invoice.component.scss'],
})
export class PartsInvoiceComponent implements OnInit {
  @Input() summaryProvider: ISummaryProvider;
  @Output() display = new EventEmitter<void>();

  purchaseSummary: IPurchaseSummary;
  showPurchaseBalance: boolean;

  errors: Array<string>;

  constructor(private userStateService: UserStateService) {}

  ngOnInit(): void {
    this.summaryProvider.load(this.loaded, this.failed);

    this.showPurchaseBalance = this.userStateService.hasRolePermission(RolePermissions.INTERFAX_SHOW_PURCHASE_BALANCE);
  }

  loaded = (ps: IPurchaseSummary): void => {
    this.purchaseSummary = ps;
    this.display.next();
  };

  failed = (err: any): void => {
    console.log('PartsInvoiceComponent.failed', err);

    this.errors = [err];
  };
}
